@charset "UTF-8";
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

/* exo-2-100 - cyrillic */
@font-face {
  font-family: "Exo 2";
  font-style: normal;
  font-weight: 100;
  src: url("../fonts/exo-2-v9-cyrillic-100.eot");
  /* IE9 Compat Modes */
  src: local(""), url("../fonts/exo-2-v9-cyrillic-100.eot?#iefix") format("embedded-opentype"), url("../fonts/exo-2-v9-cyrillic-100.woff2") format("woff2"), url("../fonts/exo-2-v9-cyrillic-100.woff") format("woff"), url("../fonts/exo-2-v9-cyrillic-100.ttf") format("truetype"), url("../fonts/exo-2-v9-cyrillic-100.svg#Exo2") format("svg"); }

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-size: 14px;
  line-height: 30px;
  word-wrap: break-word;
  letter-spacing: normal;
  text-align: center;
  background-position: top left;
  background-size: 70px 140px; }
  @media (max-resolution: 96dpi), (max-resolution: 1dppx) {
    body {
      background-image: url("../img/bg@1x.png"); } }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    body {
      background-image: url("../img/bg@2x.png"); } }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.products {
  min-height: 680px;
  padding-top: 34px;
  padding-bottom: 65px; }
  @media (max-width: 709px) {
    .products {
      width: 320px; } }
  @media (min-width: 710px) and (max-width: 1279px) {
    .products {
      width: 690px; } }
  @media (min-width: 1280px) {
    .products {
      width: 1280px; } }

.products__centering-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: auto;
  margin-right: auto; }
  @media (min-width: 1280px) {
    .products__centering-wrapper {
      width: 1120px; } }

.products__h2 {
  margin-top: 0;
  margin-bottom: 35px; }

.products__list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none; }

.h2 {
  font-family: "Exo 2", "Arial", sans-serif;
  font-weight: 100;
  font-size: 36px;
  line-height: 48px;
  color: #ffffff;
  text-shadow: 0 1px 1px;
  text-align: center; }
  @media (max-width: 709px) {
    .h2 {
      width: 320px; } }
  @media (min-width: 710px) {
    .h2 {
      width: 460px; } }

.product-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: -13px;
  margin-bottom: -20px; }
  @media (max-width: 709px) {
    .product-list {
      flex-direction: column; } }
  @media (min-width: 710px) and (max-width: 1279px) {
    .product-list {
      flex-wrap: wrap-reverse;
      justify-content: space-around;
      width: 100%;
      /* Для IE11, так как в нем по умолчанию width: auto; */ } }
  @media (min-width: 1280px) {
    .product-list {
      justify-content: space-between;
      width: 100%; } }

.product-list__item {
  margin-bottom: 50px; }
  @media (min-width: 710px) and (max-width: 1279px) {
    .product-list__item:first-child {
      order: 1; } }

.product-card {
  position: relative;
  width: 320px;
  min-height: 438px;
  margin-top: 43px;
  background-color: #f2f2f2;
  background-size: 368px 360px;
  background-position: -28px 165px;
  background-repeat: no-repeat;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom: 4px solid #1698d9;
  border-left: 4px solid #1698d9;
  border-right: 4px solid #1698d9;
  cursor: pointer; }
  @media (max-resolution: 96dpi), (max-resolution: 1dppx) {
    .product-card {
      background-image: url("../img/product-card-bg@1x.png"); } }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .product-card {
      background-image: url("../img/product-card-bg@2x.png"); } }
  .product-card::before {
    content: "";
    position: absolute;
    top: -43px;
    left: 40px;
    width: 276px;
    height: 46px;
    background-color: #f2f2f2;
    border-top: 4px solid #1698d9;
    border-top-right-radius: 10px;
    border-right: 4px solid #1698d9; }
  .product-card::after {
    content: "";
    position: absolute;
    top: -40px;
    left: 14px;
    width: 32px;
    height: 60px;
    background-color: #f2f2f2;
    border-left: 4px solid #1698d9;
    transform: rotate(45.5deg); }
  .product-card--default-hover {
    border-color: #2ea8e6; }
    .product-card--default-hover::before {
      border-color: #2ea8e6; }
    .product-card--default-hover::after {
      border-color: #2ea8e6; }
    .product-card--default-hover .product-card__weight-mark {
      background-color: #2ea8e6; }
    .product-card--default-hover .product-card__buy-word {
      color: #2ea8e6; }
      .product-card--default-hover .product-card__buy-word::after {
        border-color: #2ea8e6; }
  .product-card--selected {
    border-color: #d91667; }
    .product-card--selected::before {
      border-color: #d91667; }
    .product-card--selected::after {
      border-color: #d91667; }
    .product-card--selected .product-card__weight-mark {
      background-color: #d91667; }
    .product-card--selected .product-card__buy-word {
      color: #d91667; }
      .product-card--selected .product-card__buy-word::after {
        border-color: #d91667; }
  .product-card--disabled {
    cursor: default;
    border-color: #b3b3b3;
    color: #d2d2d2; }
    .product-card--disabled .product-card__overlay {
      background-color: rgba(242, 242, 242, 0.8); }
    .product-card--disabled::before {
      border-color: #b3b3b3; }
    .product-card--disabled::after {
      border-color: #b3b3b3; }
    .product-card--disabled .product-card__weight-mark {
      background-color: #b3b3b3; }
    .product-card--disabled .product-card__buy-word {
      color: #b3b3b3; }
      .product-card--disabled .product-card__buy-word::after {
        border-color: #b3b3b3; }
    .product-card--disabled .product-card__h3 {
      color: #d2d2d2; }
    .product-card--disabled .product-card__subtitle {
      color: #d2d2d2; }
    .product-card--disabled .product-card__promo-words {
      color: #ffff66; }

.product-card__overlay {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 434px;
  padding-top: 3px;
  padding-left: 54px;
  padding-right: 66px;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px; }

.product-card__header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 211px;
  margin-bottom: 16px; }

.product-card__overtitle {
  position: absolute;
  z-index: 1;
  top: -21px;
  left: 44px;
  width: inherit;
  font-size: 16px;
  line-height: 19px;
  color: #666666; }
  .product-card--disabled .product-card__overtitle {
    color: #d2d2d2; }

.product-card__h3 {
  align-self: center;
  margin-top: 0;
  margin-bottom: 0; }

.product-card__subtitle {
  margin-top: -2px;
  margin-left: 3px;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px; }

.product-card__p {
  align-self: flex-start;
  margin-top: 0;
  margin-left: -6px; }
  .product-card--disabled .product-card__p {
    color: #d2d2d2; }

.product-card__weight-mark {
  position: absolute;
  bottom: 12px;
  right: 11px;
  display: flex;
  flex-direction: column;
  width: 80px;
  min-height: 80px;
  padding-top: 24px;
  text-align: center;
  line-height: 22px;
  color: #ffffff;
  background-color: #1698d9;
  border-radius: 50%; }

.product-card__weight-value {
  font-size: 42px; }

.product-card__weight-unit {
  font-size: 21px; }

.product-card__promo-words {
  position: absolute;
  top: calc(100% + 18px);
  left: 0;
  width: calc(100% + 8px);
  margin-left: -5px;
  font-size: 13px;
  line-height: 15px;
  color: #ffffff;
  cursor: default; }

.product-card__buy-word {
  position: relative;
  color: #1698d9;
  /* Chrome, Edge, Safary, Firefox */
  cursor: pointer; }
  .product-card__buy-word::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 4px;
    width: 70%;
    height: 1px;
    border-bottom: 1px dashed #1698d9;
    transform: scaleX(1.4); }
  @supports (display: -webkit-flex) {
    .product-card__buy-word {
      position: static;
      text-decoration: underline;
      text-decoration-style: dashed;
      border-bottom: none; }
      .product-card__buy-word::after {
        display: none; } }

.product-card__dot {
  color: #1698d9; }

.h3 {
  width: 211px;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px; }

.p {
  width: 211px;
  text-align: left;
  font-size: 14px;
  line-height: 16px;
  color: #666666; }

.p__portion-count {
  font-weight: 700; }

.p__mouse-count {
  font-weight: 700; }
