html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

/* exo-2-100 - cyrillic */
@font-face {
  font-family: "Exo 2";
  font-style: normal;
  font-weight: 100;
  src: url("../fonts/exo-2-v9-cyrillic-100.eot"); /* IE9 Compat Modes */
  src: local(""),
    /* IE6-IE8 */
    url("../fonts/exo-2-v9-cyrillic-100.eot?#iefix") format("embedded-opentype"),
    /* Super Modern Browsers */
    url("../fonts/exo-2-v9-cyrillic-100.woff2") format("woff2"),
    /* Modern Browsers */
    url("../fonts/exo-2-v9-cyrillic-100.woff") format("woff"),
    /* Safari, Android, iOS */
    url("../fonts/exo-2-v9-cyrillic-100.ttf") format("truetype"),
    /* Legacy iOS */
    url("../fonts/exo-2-v9-cyrillic-100.svg#Exo2") format("svg");
}

body {
  @include make-flex-column();
  align-items: center;
  justify-content: center;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;

  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-size: 14px;
  line-height: 30px;
  word-wrap: break-word;
  letter-spacing: normal;
  text-align: center;

  background-position: top left;
  background-size: 70px 140px;

  @media (max-resolution: $usual-dpi), (max-resolution: $usual-dppx) {
    background-image: url("../img/bg@1x.png");
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg@2x.png");
  }
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
